import { createRouter, createWebHistory } from "vue-router";
const initial = "Reg. ";
const routes = [
  {
    path: "/",
    name: "home",
    component: () => import("../views/home/HomeView.vue"),
    meta: { title: initial + "Home", requiresAuth: false },
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../views/login/pageLogin.vue"),
    meta: { title: initial + "Login", requiresAuth: false },
  },
  {
    path: "/register",
    name: "register",
    component: () => import("../views/register/registerActivity.vue"),
    meta: { title: initial + "Conference", requiresAuth: false },
  },
  {
    path: "/search",
    name: "search",
    component: () => import("../views/search/searchRegister.vue"),
    meta: { title: initial + "Search", requiresAuth: false },
  },
  {
    path: "/dashboard",
    name: "dashboard",
    component: () => import("../views/dashboard/dashboardRegister.vue"),
    meta: { title: initial + "Dashboard", requiresAuth: false },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  const title = to.meta.title;
  if (title) document.title = title;
  const user = "";
  if (to.meta.requiresAuth) {
    try {
      console.log("1");
      if (to.name !== "login" && user != "") next({ name: "login" });
      else next();
    } catch (error) {
      console.log("2");
      next();
    }
  } else {
    console.log("3");
    next();
  }
});
export default router;
